<template>
  <div class="header-seach-comm ">
    <header>
      <div class="share"><i class="back-img" @click="toback"></i></div>
      <div class="search-bar">
        <div class="search-bar-tool"><input type="text" placeholder="搜索" @click.prevent="toSearch"></div>
      </div>
      <div class="share"><i class="share-img" @click="share" id="share"></i></div>
    </header>
    <section class="classify-content">
      <div class="hi-life-list-item" v-for="(item,key) in classifyList.list" @click="goStore(item)">
        <div class="hi-life-list-item-img"><img :src="item.imageUrl || '/'"></div>
        <div class="hi-life-list-item-content">
          <h3 class="hi-life-list-item-title">{{item.name}}<span class="store-distance">{{0 || item.distance|formatKm}}</span></h3>
          <div class="hi-life-list-item-summary">{{item.summary}}</div>
          <div class="hi-life-list-item-other">销量:<span>{{item.sellAmount}}</span></div>
        </div>
      </div>
      <p class="loadMore"><span @click="loadMore">{{loadText}}</span><mt-spinner type="fading-circle" v-if="isLoading"></mt-spinner></p>
    </section>
  </div>
</template>

<script>
  import {getByStoreCategory} from '../../service/getData'
  export default {
    data () {
      return {
        classifyList: {},
        loadText: '加载更多',
        isLoading: false,
        pages: 0,
        pageNum: 1,
        pageSize: 1,
        id:'',
      }
    },
    created: function () {
      let _this = this
      window.addEventListener('click',function (e) {
        if (_this.shareList && e.target.id !== 'share') {
          _this.shareList = false
        }
      })
      this. id = this.$route.query.classifyId
      getByStoreCategory(this.pageNum,this.pageSize,this.id,localStorage.latitude,localStorage.longitude)
        .then(function (response) {
          _this.classifyList = response.data
          _this.pages = response.data.pages
          _this.pageNum = response.data.pageNum
          if (response.data.pages === response.data.pageNum) {
            _this.loadText = '没有更多了'
          }
        })
        .catch(function () {
          _this.$toast('加载失败')
        })
    },
    methods: {
      toSearch: function () {
      },
      share: function () {
      },
      goStore: function (val) {
        this.$router.push({
          name: 'store',
          query: {
            storeId: val.id,
            latitude: val.latitude,
            longitude: val.longitude
          }
        })
      },
      toback: function () {
        this.$router.go(-1)
      },
      loadMore: function () { 
        let _this = this
        if (this.pages <= this.pageNum) {
          return false
        }
        this.loadText = '加载中'
        this.isLoading = true;
        if (this.pages > this.pageNum) {
          setTimeout(() => {
            getByStoreCategory(++this.pageNum,this.pageSize,this. id,localStorage.latitude, localStorage.longitude)
            .then(res => {
              for (var i =0;i<res.data.list.length;i++){
                _this.classifyList.list.splice(_this.classifyList.list.length,1,res.data.list[i])
              }
              console.log(_this.classifyList.list)
              this.isLoading = false;
              if (this.pages <= this.pageNum) {
                this.loadText = '没有更多了'
              } else {
                this.loadText = '加载更多'
              }
            })
            .catch(res => {
              console.log(res)
              _this.$toast('加载附近商家数据失败!')
            })
          },500)
        }
      }
    },
    filters: {
      formatKm: function (val) {
        return val.replace('千米','km')
      }
    }
  }
</script>

<style>
/* 分类详情页 */
.header-seach-comm>header{
  position: absolute;
}
.store-view{
  height: 100%;
  overflow: hidden;
}
.share-img {
  background: url(/static/image/classify/classify_share@3x.png);
  background-size: 100% 100%;
}

.back-img {
  background: url(/static/image/classify/classify_back@3x.png);
  background-size: 100% 100%;
}

.share:first-child {
  text-align: right;
  width: .4rem;
}

.classify-content {
  height: 100%;
  overflow: auto;
  padding-left: .15rem;
  -webkit-overflow-scrolling: touch;
}
.share-list{
  position: fixed;
  height: .8rem;
  width: .8rem;
  right: .15rem;
  top: .47rem;
  z-index: 1;
  text-align: center;
  background: rgba(0, 0, 0,.8);
}
.share-list:before {
  content: '';
  position: absolute;
  top: -.1rem;
  right: .03rem;
  border: .05rem solid;
  border-color: transparent transparent rgba(0, 0, 0,.8) transparent;
}
.share-list ul {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.share-list ul li {
  flex: 1;
  flex-shrink: 0;
  color: #FFFFFF;
  font-size: .12rem;
  border-bottom: .01rem solid rgba(255,255,255,.3);
  padding: .03rem;
}
</style>


